import { Button, Container, Row, Col } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faLock,
  faPiggyBank,
  faHandHoldingUsd,
} from "@fortawesome/free-solid-svg-icons";
import React from "react";
import CustomDivider from "./CustomDivider";
import { toLocaleString } from "./utils";
import {
  queryCollectionByTradeCountScaled,
  queryCollectionByTradeVolume,
  getPlatformStats,
  queryNftByNewest,
  queryNft,
  queryNftBySearch,
} from "./api";
import SideScroller from "./SideScroller";
import CollectionCard from "./CollectionCard";
import NFTCard from "./NFTCard";

function Main() {
  const [hotCollections, setHotCollections] = React.useState(null);
  const [newNfts, setNewNfts] = React.useState(null);
  const [recentNfts, setRecentNfts] = React.useState(null);

  React.useEffect(() => {
    (async () => {
      setHotCollections((await queryCollectionByTradeCountScaled()).results);
      setNewNfts((await queryNft({
        key: 'listHeight',
        direction: 'DESC',
        limit: 50,
        offset: 0,
      })).results);
      setRecentNfts(
        (
          await queryNft({
            key: "tradeHeight",
            direction: "DESC",
            limit: 50,
            offset: 0,
          })
        ).results
      );
    })();
  }, []);

  return (
    <div className="d-flex flex-column">
      {hotCollections !== null && (
        <div className="my-4">
          <h2 className="accent overview-title">Hot collections </h2>
          <SideScroller
            items={hotCollections}
            itemTemplate={(x) => (
              <li key={x}>
                <a href={`/collection/${x}`}>
                  <CollectionCard handle={x} />
                </a>
              </li>
            )}
          />
        </div>
      )}
      {newNfts !== null && (
        <div className="my-4">
          <h2 className="accent overview-title">Newly listed </h2>
          <SideScroller
            items={newNfts}
            itemTemplate={(x) => (
              <li key={x}>
                <a href={`/nft/${x}`}>
                  <NFTCard address={x} />
                </a>
              </li>
            )}
          />
        </div>
      )}
      {recentNfts !== null && (
        <div className="my-4">
          <h2 className="accent overview-title">Recently sold </h2>
          <SideScroller
            items={recentNfts}
            itemTemplate={(x) => (
              <li key={x}>
                <a href={`/profile/${x}`}>
                  <NFTCard address={x} />
                </a>
              </li>
            )}
          />
        </div>
      )}
    </div>
  );
}

export default Main;
